.politics-page {
    background: rgba(13, 58, 106, 0.10);
    height: 100vh;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;

    .header {
        display: flex;
        margin: 0.5rem 0;

        .title {
            color: #23262F;
            text-align: center;
            font-feature-settings: 'clig' off, 'liga' off;
            /* Heading/H4 */
            font-family: Inter, sans-serif;
            font-size: 1.5rem;
            font-style: normal;
            font-weight: 500;
            line-height: 2rem; /* 133.333% */
        }
    }

    .description {
        color: #87898E;
        font-feature-settings: 'clig' off, 'liga' off;
        font-family: Inter;
        font-size: 1rem;
        font-style: normal;
        font-weight: 400;
        line-height: 1.5rem; /* 150% */
        padding-bottom: 1rem;
        max-height: 300px;
        overflow-y: scroll;
    }

    .button-container {
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;
        width: 100%;
        margin: 1rem 0;

        .back-button {
            border-radius: 0.5rem;
            background-color: var(--primary-blue);
            cursor: pointer;
            display: flex;
            padding: 1.25rem 5.78rem;
            justify-content: center;
            align-items: center;
            gap: 1rem;
            flex-shrink: 0;
            color: var(--color-light-light, #FCFCFD);
            font-feature-settings: 'clig' off, 'liga' off;
            /* Button / Medium / Normal */
            font-family: Inter, sans-serif;
            font-size: 1rem;
            font-style: normal;
            font-weight: 400;
            line-height: 1rem; /* 100% */
            transition: 0.3s;
            opacity: 0.8;

            &:hover {
                opacity: 1;
            }
        }
    }

    @media (max-width: 768px) {
        .checkbox-container {
            display: flex;
            gap: 0.5rem;
        }
    }
}